let offset = 0; //
const sliderLine = document.getElementById('slider__line');
const leftBtn = document.getElementById('left__carousel__btn');
const rightBtn = document.getElementById('right__carousel__btn');
leftBtn.addEventListener('click', function () {
  offset += 240;
  if (offset >= 1400) {
    offset = 0;
  }
  sliderLine.style.left = -offset + 'px';
});
rightBtn.addEventListener('click', function () {
  offset -= 240;
  if (offset <= 0) {
    offset = 0;
    sliderLine.style.left = -offset + 'px';
  }
});
setInterval(() => {
  offset += 240;
  if (offset >= 1400) {
    offset = 0;
  }
  sliderLine.style.left = -offset + 'px';
}, 4000);
